
import { defineComponent } from 'vue';
import Logo from './Logo.vue';

export default defineComponent({
  name: 'Header',
  props: {
    showAbout: {
      type: Boolean,
      required: true
    },
  },
  components: {
    Logo,
  },
  setup(props, { emit }) {
    const visibilityChanged = (isVisible: boolean) => {
      if (!isVisible) {
        emit('close')
      }
    }
    return {
      visibilityChanged,
    }
  },
});
